import { Link } from 'react-router-dom'

import { BUCKET } from '../utils/Variables'
import '../styles/Shop.css'

const Shop = props => {
    let filteredProducts

    if (props.slug === 'all') {
        filteredProducts = props.products
    } else {
        filteredProducts = props.products.filter(product => product.collection === props.slug)
    }

    return(
        <div>
            <h1 className="PageTitle">Products</h1>

            <div className="CollectionsContainer PageContainer">
                { filteredProducts && filteredProducts.map(product => (
                    <Link 
                        to={`/shop/${product.slug}`}
                        key={product.slug}
                        className='CollectionThumbnail'
                    >
                        <div className="ProductImageContainer">
                            <img className="ProductImage" src={`${BUCKET}/product_images/${product.slug}/${product.images[0]}`} alt='product' />
                        </div>
                        <h1>{product.name}</h1>
                        <p>${product.price}</p>
                        <p className="ProductDescription">{product.description}</p>
                    </Link>
                ))}
            </div>
        </div>
    )
}

export default Shop