import { Link } from 'react-router-dom'

import { BUCKET } from '../utils/Variables'

import '../styles/Collections.css'

const Collections = props => {
    // build out collections from products dynamically
		let collections = []  
		
    for (let i = 0; i < props.products.length; i++) {
				let matched
				
        for (let j = 0; j < collections.length; j++) {
            if (props.products[i].collection === collections[j].collection) {
                matched = true
            }
				}
				
        if (!matched) {
            collections.push(props.products[i])
        }
		}
		
    return(
        <div>
            <h1 className="PageTitle">Collections</h1>
            <div className="CollectionsContainer PageContainer">
                <Link
                to='/collections/all'
                key='all-products'
                className='CollectionThumbnail'
                >
                    <div className="ProductImageContainer">
                        <img className="ProductImage" src={`${BUCKET}/product_images/cordyceps/1.jpeg`} alt="array of products" />
                    </div>
                    <h1>All Products</h1>
                </Link>
				{ 
					collections.map(col => (
						<Link
						to={ `/collections/${ col.collection }`}
						key={ col._id }
						className='CollectionThumbnail'
						>
                            <div className="ProductImageContainer">
                                <img className="ProductImage" src={`${BUCKET}/product_images/${ col.slug}/${col.images[0]}`} alt={col.name} />
                            </div>
							<h1>{ col.collection }<span>s</span></h1>
						</Link>
					)) 
				}
            </div>
        </div>
    )
}

export default Collections